"use client";

import { trpc } from "@/lib/trpc";
import { Card, CardContent, CardHeader, CardTitle } from "@repo/ui/components/card";
import { DataTable } from "@repo/ui/components/data-table";
import { ColumnDef } from "@tanstack/react-table";
import { DollarSign, Users, Activity, CreditCard } from "lucide-react";

// Definice sloupců pro tabulku (zkrácená verze)
type User = { id: string; name: string; email: string; role: string; status: string };
const columns: ColumnDef<User>[] = [
  { accessorKey: "name", header: "Jméno" },
  { accessorKey: "email", header: "Email" },
  { accessorKey: "role", header: "Role" },
  { accessorKey: "status", header: "Status" },
];

export default function DashboardPage() {
  const { data: users } = trpc.getUsers.useQuery();

  return (
    <div className="flex flex-col gap-8">
      
      {/* 1. SEKCE: STATISTICKÉ KARTY (WIDGETS) */}
      <div className="grid gap-4 md:grid-cols-2 md:gap-8 lg:grid-cols-4">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Celkové příjmy</CardTitle>
            <DollarSign className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">452,000 Kč</div>
            <p className="text-xs text-muted-foreground">+20.1% oproti minulému měsíci</p>
          </CardContent>
        </Card>
        
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Uživatelé</CardTitle>
            <Users className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">+2350</div>
            <p className="text-xs text-muted-foreground">+180 nových tento týden</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Prodeje</CardTitle>
            <CreditCard className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">+12,234</div>
            <p className="text-xs text-muted-foreground">+19% nárůst</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Aktivita</CardTitle>
            <Activity className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">Hodně</div>
            <p className="text-xs text-muted-foreground">+201 od poslední hodiny</p>
          </CardContent>
        </Card>
      </div>

      {/* 2. SEKCE: TABULKA POSLEDNÍCH UŽIVATELŮ */}
      <div className="grid gap-4 md:gap-8 lg:grid-cols-1">
        <Card className="xl:col-span-2">
          <CardHeader className="flex flex-row items-center">
            <div className="grid gap-2">
              <CardTitle>Poslední registrace</CardTitle>
              <p className="text-sm text-muted-foreground">
                Seznam uživatelů načtený z backendu.
              </p>
            </div>
          </CardHeader>
          <CardContent>
             <DataTable columns={columns} data={users || []} />
          </CardContent>
        </Card>
      </div>
    </div>
  );
}